import { useEffect, useRef } from "react";

/**
 * A custom hook that is only triggers on updates, not on initial mount
 * @param fn
 * @param inputs
 */
export const useDidUpdateEffect = (fn: () => void, inputs: [string[] | string | undefined]): void => {
	const didMountRef = useRef(false);

	useEffect(() => {
		if (didMountRef.current) {
			fn();
		} else {
			didMountRef.current = true;
		}
	}, [fn, inputs]);
};
